/*! normalize.css v3.0.3 | MIT License | github.com/necolas/normalize.css */
/**
 * 1. Set default font family to sans-serif.
 * 2. Prevent iOS and IE text size adjust after device orientation change,
 *    without disabling user zoom.
 */
html {
  font-family: sans-serif;
  /* 1 */
  -ms-text-size-adjust: 100%;
  /* 2 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
}

/**
 * Remove default margin.
 */
body {
  margin: 0;
  background-image: url(../img/content/background.png);
}

header {
  background-image: url(../img/content/header_stripe.png);
  height: 45px;
}

.search {
  float: right;
  width: 250px;
  height: 30px;
  margin-right: 11.66667%;
  margin-top: 25px;
  padding: none;
  border: 1px solid #000;
}

input[type="text"] {
  border: none;
  outline: none;
  vertical-align: middle;
  width: 220px;
  height: 25px;
}

input[type="submit"] {
  width: 21px;
  height: 21px;
  border: none;
  background-image: url("../img/content/search.png");
  background-color: white;
  background-position: center;
  background-repeat: no-repeat;
  vertical-align: middle;
}

input[type="submit"]:hover {
  cursor: pointer;
}

.links {
  vertical-align: middle;
  color: blue;
  font-size: 12px;
  height: 100%;
  font-family: "Sans Bold";
}

.slider__main {
  text-align: center;
  margin: 0 auto;
  max-width: 1100px;
  margin-top: 30px;
}

.slider__logo {
  width: 267px;
  height: auto;
  margin-left: 13.58333%;
  margin-right: 1.66667%;
  margin-bottom: 10px;
  margin-top: 10px;
  float: left;
}

.slider__plane {
  width: 91.66667%;
  height: auto;
}

.slider__name-top {
  position: absolute;
  top: 37.53754%;
  left: 16.66667%;
  color: white;
  font-size: 48px;
  text-align: left;
  font-family: "HeliosLight";
}

.slider__name-middle {
  position: absolute;
  top: 40.54054%;
  left: 16.66667%;
  color: white;
  font-size: 76px;
  text-align: left;
  font-family: "HeliosThin";
}

.slider__name-bottom {
  position: absolute;
  top: 75.07508%;
  left: 16.66667%;
  color: white;
  font-size: 14px;
  text-align: left;
  font-family: "HeliosLight";
}

.links__usa, .links__deu, .links__rus, .links__online, .links__enter {
  display: inline-block;
  margin: 0 auto;
  margin-top: 12px;
  cursor: pointer;
}

.links__usa:hover, .links__deu:hover, .links__rus:hover, .links__online:hover, .links__enter:hover {
  color: orange;
}

.links__usa {
  margin-left: 14.58333%;
}

.links__deu, .links__rus, .links__enter {
  margin-left: 30px;
}

.links__online {
  margin-left: 440px;
}

.links__enter {
  color: blue;
}

.links__clock {
  margin: 0 auto;
  width: 20px;
  height: 20px;
}

.container {
  width: 1100px;
  height: auto;
  margin: 0 auto;
  overflow: auto;
}

.slider-inner {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  float: left;
  padding: 3px;
}

.slider-inner img {
  display: none;
  width: 100%;
  height: auto;
}

.slider-inner img.active {
  display: inline-block;
  width: 100%;
  height: auto;
}

.slider-inner p.active {
  display: inline-block;
  width: 100%;
  height: auto;
}

.prev, .next {
  float: left;
  margin-top: 180px;
  cursor: pointer;
}

.prev {
  position: relative;
  margin-right: -65px;
  margin-left: 30px;
  z-index: 100;
}

.next {
  position: relative;
  margin-left: -55px;
  z-index: 100;
}

.ionTabs {
  position: relative;
}

.ionTabs__head {
  position: relative;
  display: block;
  list-style-type: none;
  margin: 0;
  padding: 0;
  z-index: 1;
}

.ionTabs__head:before, .ionTabs__head:after {
  content: "";
  display: table;
}

.ionTabs__head:after {
  clear: both;
}

.ionTabs__head {
  zoom: 1;
}

.ionTabs__tab {
  position: relative;
  display: block;
  float: left;
  list-style-type: none;
  background: none;
  margin: 0;
  padding: 0;
  cursor: pointer;
  font-family: "Sans Regular";
}

.ionTabs__tab.ionTabs__tab_state_active {
  cursor: default;
}

.ionTabs__body {
  position: relative;
  z-index: 2;
}

.ionTabs__item {
  position: relative;
  display: none;
}

.ionTabs__item.ionTabs__item_state_active {
  display: block;
}

.ionTabs__preloader {
  position: relative;
}

.ionTabs {
  margin: 0 0 30px;
}

.ionTabs__head {
  border-bottom: 1px solid #cecece;
}

.ionTabs__tab {
  font-size: 16px;
  line-height: 36px;
  border: none;
  padding: 12px 25px 11px;
  color: #484848;
  margin-top: 20px;
  text-align: inherit;
  min-width: 50px;
}

.ionTabs__tab.ionTabs__tab_state_active {
  top: 1px;
  margin-top: 20px;
  padding: 12px 25px 11px;
  background: #fff;
  color: #d71410;
  border: 1px solid #cecece;
  border-top-color: #d71410;
  border-bottom: 1px solid #fff;
}

.ionTabs__body {
  background: #fff;
  border: 1px solid #cecece;
  border-top: none;
  border-bottom: none;
}

.ionTabs__preloader {
  height: 50px;
  background: url(../img/content/preloader-flat.gif) no-repeat 25px 50%;
}

#lean_overlay {
  position: fixed;
  z-index: 100;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  background: #000;
  display: none;
}

#basic-modal {
  display: none;
}

.modal {
  width: 600px;
  height: 200px;
  padding: 30px;
  display: none;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.7);
  color: black;
}

.input-window {
  margin-bottom: 15px;
}

.input-button {
  margin-left: 20px;
  width: 80px;
}

#email, #password {
  width: 150px;
}

@media (max-width: 1024px) and (min-width: 799px) {
  .links__online {
    margin-left: 220px;
  }
  .slider__logo {
    margin-left: 2%;
  }
  .search {
    margin-right: 2%;
    margin-top: 25px;
  }
  .container {
    width: 91.66667%;
  }
  .slider__name-top {
    font-size: 36px;
  }
  .slider__name-middle {
    font-size: 64px;
  }
  .slider__name-bottom {
    font-size: 10px;
  }
  .prev, .next {
    margin-top: 160px;
  }
}

@media (max-width: 800px) and (min-width: 639px) {
  .links__online {
    margin-left: 120px;
  }
  .links__usa {
    margin-left: 10%;
  }
  .slider__logo {
    margin-left: 2%;
    width: 197px;
    height: auto;
  }
  .search {
    width: 180px;
  }
  input[type="text"] {
    width: 150px;
  }
  .ionTabs__tab {
    padding: 6px 15px 5px;
  }
  .ionTabs__tab.ionTabs__tab_state_active {
    padding: 6px 15px 5px;
  }
  .slider__name-top {
    font-size: 32px;
  }
  .slider__name-middle {
    font-size: 50px;
  }
  .slider__name-bottom {
    font-size: 8px;
  }
  .prev, .next {
    margin-top: 120px;
  }
  .container {
    width: 91.66667%;
  }
}

@media (max-width: 640px) and (min-width: 319px) {
  .links__online {
    margin-left: 60px;
  }
  .links__usa {
    margin-left: 5%;
  }
  .slider__logo {
    margin-left: 2%;
    width: 137px;
    height: auto;
  }
  .search {
    width: 150px;
    margin-right: 4%;
  }
  input[type="text"] {
    width: 120px;
  }
  .ionTabs__tab {
    font-size: 14px;
    padding: 3px 9px 2px;
  }
  .ionTabs__tab.ionTabs__tab_state_active {
    padding: 3px 9px 2px;
  }
  .container {
    width: 91.66667%;
  }
  .prev, .next {
    margin-top: 90px;
  }
  .slider__name-top {
    font-size: 28px;
  }
  .slider__name-middle {
    font-size: 44px;
  }
  .slider__name-bottom {
    font-size: 7px;
  }
}

@media (max-width: 320px) and (min-width: 239px) {
  header {
    height: 55px;
  }
  .ionTabs__tab {
    margin-left: 10px;
  }
  .slider__logo {
    margin-bottom: 12px;
  }
  .slider__name-top {
    font-size: 14px;
  }
  .slider__name-middle {
    font-size: 24px;
  }
  .slider__name-bottom {
    font-size: 7px;
  }
  .prev, .next {
    margin-top: 40px;
    height: auto;
    width: 20px;
  }
}

